.slideshow-wrapper {
  background-color: $color-blue-dark;
}

.slideshow {
  height: 100%;
  width: 100%;
  position: relative;
  white-space: nowrap;
  transition: margin-left 1s;

  .slide {
    height: 100%;
    width: 100%;
    position: relative;
    display: inline-block;
    white-space: normal;
  }
}