/* marvel-regular - latin */
@font-face {
  font-family: 'Marvel';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/marvel/marvel-v6-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Marvel'), local('Marvel-Regular'),
  url('../fonts/marvel/marvel-v6-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/marvel/marvel-v6-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/marvel/marvel-v6-latin-regular.woff') format('woff'), /* Modern Browsers */
  url('../fonts/marvel/marvel-v6-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
  url('../fonts/marvel/marvel-v6-latin-regular.svg#Marvel') format('svg'); /* Legacy iOS */
}
/* marvel-700 - latin */
@font-face {
  font-family: 'Marvel';
  font-style: normal;
  font-weight: 700;
  src: url('../fonts/marvel/marvel-v6-latin-700.eot'); /* IE9 Compat Modes */
  src: local('Marvel Bold'), local('Marvel-Bold'),
  url('../fonts/marvel/marvel-v6-latin-700.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/marvel/marvel-v6-latin-700.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/marvel/marvel-v6-latin-700.woff') format('woff'), /* Modern Browsers */
  url('../fonts/marvel/marvel-v6-latin-700.ttf') format('truetype'), /* Safari, Android, iOS */
  url('../fonts/marvel/marvel-v6-latin-700.svg#Marvel') format('svg'); /* Legacy iOS */
}